import React, {Component} from 'react';
import { Link } from 'react-router-dom';
import './App.css';


class Header extends Component {
    constructor() {
        super();

        this.state = {
            showMenu: false,
        }

        this.showMenu = this.showMenu.bind(this);
    }

    showMenu(event) {
        event.preventDefault();

        this.setState(state => ({
            isToggleOn: !state.isToggleOn
        }));
    }
    render() {
        return(
            <>
            <header>
                <nav className="mynav">
                    <div>
                        <button onClick={this.showMenu}>
                            <i className="fa fa-align-justify"></i> Catalog <i className="fa fa-angle-down"></i>
                        </button>
                        {
                            this.state.isToggleOn
                                ? (
                                    <div className="menu">
                                        <Link to="/dining-sets"><i className="fa fa-angle-right"></i> Dining Sets </Link>
                                        <Link to="/bar-sets"><i className="fa fa-angle-right"></i> Bar Sets </Link>
                                        <Link to="/deep-seating"><i className="fa fa-angle-right"></i> Deep Seating </Link>
                                        <Link to="/extendable-table-sets"><i className="fa fa-angle-right"></i> Extendable Table Sets </Link>
                                    </div>
                                )
                                : (
                                    null
                                )
                        }
                    </div>
                </nav>
                <div className="logo">
                    <button><Link className="logo" to="/"><i className="fa fa-home"></i></Link></button>
                </div>
                <div className="cart"><button><i className="fa fa-shopping-cart"></i></button></div>
            </header>
            </>
        )
    }
}

export default Header;