import React, {Component} from 'react';
import { Link } from 'react-router-dom';
import Book from './Book';
import axios from "axios/index";
import './Product.css';


class Products extends Component {

    static defaultProps = {
        books: [
            {id: 1, title: "PHP Book 7.3", type: "Advance", year: 2019},
            {id: 2, title: "Magento Book 2.3.2", type: "Advance", year: 2019},
        ]
    }

    state = { open: false, data: [], loading: false }

    componentDidMount() {
        this.setState({loading: true})

        axios({
            url: 'https://cors-anywhere.herokuapp.com/http://patiofurnitureonsale.com/graphql',
            method: 'post',
            data: {
                query: `
                      {
                  products(
                  sort: {price: DESC }
                  filter: {
                          category_id : {
                            eq: "3"
                          }
                        }
                  search: "patio"
                  pageSize: 8
                  )
                  {
                    total_count
                    items {
                      name
                      sku
                      upc
                      canonical_url
                      price {
                    regularPrice {
                      amount {
                                value
                                currency
                              }
                            }
                          }
                      thumbnail {
                            label
                            url
                          }
                    }
                  }
                }
            `
            }
        })
        .then((result) => {
            console.log(result.data.data.products.items)
            this.setState({data: result.data.data.products.items, loading: false})
        }).catch((error) => {
        console.log(error)
        });
    }


    render() {
        const { books } = this.props
        return(
        <div>
            { this.state.loading
            ? <div className="spinner"><div className="bounce1"></div><div className="bounce2"></div><div className="bounce3"></div></div>
            : <div className="content">
                {this.state.data.map(product => {
                    return(
                    <div className="Card" key={ product.sku }>
                        <img src={product.thumbnail.url} alt={product.name} />
                        <div className="Container">
                            <p><strong className="price">Price: </strong> <i className="fa fa-dollar"></i>{ product.price.regularPrice.amount.value }</p>
                            <Link to={`/single-product/${product.sku}`}>
                            <button><i className="fa fa-cart-arrow-down"></i> Explore</button>
                            </Link>
                        </div>
                    </div>
                    )
                })}
            </div>
            }
            {books.map(
                book => <Book key={book.id} title={book.title} type={book.type} year={book.year} />
            )}
        </div>
        )
    }}

export default Products;