import React, {Component} from 'react';
import './App.css';


class Contact extends Component {
    constructor() {
        super();

        this.state = {
            showMenu: false,
        }

        this.showMenu = this.showMenu.bind(this);
    }

    showMenu(event) {
        event.preventDefault();

        this.setState(state => ({
            isToggleOn: !state.isToggleOn
        }));
    }
    render() {
        return(
            <>
            <h1>Contact us</h1>
            </>
        )
    }
}

export default Contact;