import React, {Component} from 'react';
import 'font-awesome/css/font-awesome.min.css';
import {Link} from 'react-router-dom';
import axios from 'axios';

const API_PATH = 'https://cors-anywhere.herokuapp.com/https://patiofurnitureonsale.com/api/contact/index.php';

class PlaceOrder extends Component {

    constructor(props) {
        super(props);
        this.state = {name: '', email: '', full_address: '', mailSent: false, error: null};

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    handleSubmit(e) {
        e.preventDefault();

        axios({
            method: 'post',
            url: `${API_PATH}`,
            headers: { 'content-type': 'application/json' },
            data: this.state
        })
            .then(result => {
                this.setState({
                    mailSent: result.data.sent
                })
            })
            .catch(error => this.setState({ error: error.message }));
    }

    render() {
        let sku = this.props.match.params.sku;
        return(
            <>
            <div className="place-order">
                <Link to={`/single-product/${sku}`}>
                    <p><i className="fa fa-arrow-left"></i> Back to Product</p>
                </Link>
            <h2>Place Order</h2>
                <p>Item Id: #{sku}</p>
            <form onSubmit={this.handleSubmit}>
            <input type="text" value={this.state.name} name='name' onChange={e => this.handleChange(e)} placeholder="Full Name"/>
            <input type="email" value={this.state.email} name="email" onChange={e => this.handleChange(e)} placeholder="E-mail"/>
            <input type="text" value={this.state.address} name="full_address" onChange={e => this.handleChange(e)} placeholder="Full Address"/>
            <input type="tel" value={this.state.tel} name="phone_number" onChange={e => this.handleChange(e)} placeholder="Contact Phone number"/>
                <button >Submit Order</button>
                <div>
                    {this.state.mailSent &&
                    <div>Thank you for contcting us.</div>
                    }
                </div>
            </form>
            </div>
            </>
        )
    }
}

export default PlaceOrder;