import React, {Component} from 'react';
import Item from "../Item";
import Data from '../data/extendable';

class ExtendableSet extends Component {

    static defaultProps = Data;

    render() {
        const items = this.props.data.products.items;
        return(
            <div className="content">
                <h1 className="page-title">Outdoor Patio Extendable Dining Table Sets</h1>
                {items.map(
                item => <Item key={item.name}
                              title={item.name}
                              sku={item.sku}
                              price={item.price.regularPrice.amount.value}
                              image={item.image.url}
                               />
            )}
            </div>
        )
    }
}

export default ExtendableSet;