import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './Product.css';
import axios from "axios/index";


class SingleProduct extends Component {

    static defaultProps = {
        books: [
            {id: 1, title: "PHP Book 7.3", type: "Advance", year: 2019},
            {id: 2, title: "Magento Book 2.3.2", type: "Advance", year: 2019},
        ]
    }

    state = { open: false, data: [], loading: false }

    componentDidMount() {
        this.setState({loading: true})
        axios({
            url: 'https://cors-anywhere.herokuapp.com/http://patiofurnitureonsale.com/graphql',
            method: 'post',
            data: {
                query: `
                      query($sku: String!){
                  products(
                    filter: {
                        sku: {eq: $sku}
                    }
                  )
                  {
                    total_count
                    items {
                      name
                      sku
                      upc
                      canonical_url
                      price {
                    regularPrice {
                      amount {
                        value
                        currency
                      }
                    }
                  }
                      image {
                            label
                            url
                          }
                    }
                  }
                }
            `,
                variables: {
                    sku: this.props.match.params.name
                }
            }

        })
            .then((result) => {
                console.log(result.data.data.products.items)
                this.setState({data: result.data.data.products.items, loading: false})
            }).catch((error) => {
            console.log(error)
        });
    }


    render() {
        return(
            <div>
                { this.state.loading
                    ? <div className="spinner"><div className="bounce1"></div><div className="bounce2"></div><div className="bounce3"></div></div>
                    : <div className="content">
                        {this.state.data.map(product => {
                            return(
                                <div className="singleCard" key={ product.sku }>
                                    <button className="back-button" onClick={this.props.history.goBack}><i className="fa fa-arrow-left"></i> Go Back</button>
                                    <img src={product.image.url} alt={product.name} width="500" />
                                    <div className="Container">
                                        <h4>{ product.name }</h4>
                                        <p><strong>UPC: </strong>{ product.upc } - <strong>SKU: </strong>{ product.sku }</p>
                                        <p><strong className="price">Price: </strong> ${ product.price.regularPrice.amount.value }</p>
                                        <Link to={`/order/${product.sku}`}>
                                            <button>Place Order</button>
                                        </Link>
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                }
            </div>
        )
    }}

export default SingleProduct;